import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';

const Section = styled.section`
    border: 3px solid black;
    padding: 1rem;
    margin-bottom: 1.8rem;
    border-radius: 6px;
    background-color: rgba(0,0,0,0.2);
    box-shadow: 1px 1px 1px 0.8px;
`;

const Button = styled.button`
    margin: 0 8px;
`;

const BalanceToggleButton = styled(Button)`
    width: 150px;
`;

const formatter = Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
})

export default function AccountBalance(props) {
    const buttonClass = 'btn ' + (props.showBalance ? 'btn-warning' : 'btn-info');

    const handleClick = (e) => {
        e.preventDefault();
        props.toggleBalance();
    }

    const handleHelicopterClick = (e) => {
        e.preventDefault();
        props.helicopterDrop();
    }

    const buttonText = props.showBalance ? 'Hide Balance' : 'Show Balance';

    return (
        <Section className="account-balance">
            {props.showBalance && <p>Account Balance: <span>{formatter.format(props.amount)}</span></p>}
            <BalanceToggleButton className={buttonClass} onClick={handleClick}>{buttonText}</BalanceToggleButton>
            <Button className='btn btn-success' onClick={handleHelicopterClick}><i className="fa fa-helicopter"/></Button>
        </Section>
    );
}


AccountBalance.propTypes = {
    amount: PropTypes.number.isRequired,
}




