import React from 'react'
import { PropTypes } from 'prop-types';
import styled from 'styled-components';

const Td = styled.td`
    width: 120px;
    padding: 0.3rem;
`;

const Form = styled.form`
    display: flex;
    justify-content: space-evenly;
`;

const Button = styled.button`
    margin-right: 0.3rem;
`;

const TdControls = styled(Td)`
`;

export default function Coin(props) {

    const handleClick = (e) => {
        e.preventDefault();
        props.handleRefresh(props.id);
    }

    const handleClickBuy = (e) => {
        e.preventDefault();
        props.handleBuy(props.ticker, props.price, props.id)
    }

    const handleClickSell = (e) => {
        e.preventDefault();
        props.handleSell(props.ticker, props.price, props.id, props.balance)
    }

    return (
        <tr>
        <Td>{props.name}</Td>
        <Td>{props.ticker}</Td>
        <Td>${props.price}</Td>
        {props.showBalance && <Td>{props.ticker} {props.balance}</Td>}
        <TdControls>
            <Form action="#" method="POST">
                <Button className='btn btn-info' onClick={handleClick}>Refresh</Button>
                <Button className='btn btn-success' onClick={handleClickBuy}>Buy</Button>
                <Button className='btn btn-danger' onClick={handleClickSell}>Sell</Button>
            </Form>
        </TdControls>
    </tr>
    );
}

Coin.propTypes = {
    id: PropTypes.string.isRequired,
    name: PropTypes.string.isRequired,
    ticker: PropTypes.string.isRequired,
    price: PropTypes.number.isRequired,
}




